.wrapper {
  display: block;
}

.control {
  display: block;
  margin: 5rem auto;
  width: 30%;
  box-shadow: 0 0 1px 1px rgb(211, 210, 210);
  border-radius: 8px;
  text-align: center;
  padding: 20px 1rem;
}

.control label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-align: left;
}

.control input,
.control textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.5rem;
  border-radius: 12px;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
}

.control input:focus,
.control textarea:focus {
  outline: none;
  border-color: #230052;
}

